.container {
    display: flex;
    flex-direction: column;
}

.serviceContainer {
    position: relative;
    display: flex;
    padding-bottom: 1px;
}

.serviceInput {
    background-color: var(--tg-theme-bg-color);    
    height: 44px;
    border: none;
    color: var(--tg-theme-text-color);
    font-size: 17px;
    line-height: 22px;
    letter-spacing: -0.43px;
    font-family: 'SF Pro Regular';
    outline: none;



    padding-left: 16px;

    width: 100%;
    box-sizing: border-box;
    max-width: 100%;
}

.serviceInput::placeholder {
    color: var(--tg-theme-hint-color);
    font-size: 17px;
    line-height: 22px;
    letter-spacing: -0.43px;
    font-family: 'SF Pro Regular';
}

.serviceName {
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
}

.servicePrice {
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
}

.borderBottom {
    position: absolute;
    bottom: 0;
    left: 16px;
    right: 0;
    border-bottom: 1px solid var(--tg-theme-secondary-bg-color);

    /* border-bottom: 1px solid black; */
    pointer-events: none;
}

.btn {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: var(--tg-theme-bg-color);    
    height: 44px;
    border: none;

    color: var(--tg-theme-hint-color);
    font-size: 17px;
    line-height: 22px;
    letter-spacing: -0.43px;
    font-family: 'SF Pro Regular';



    padding-left: 16px;

    padding-right: 17px;

    /* padding: 0 16px; */

    width: 100%;
    box-sizing: border-box;
    max-width: 100%;
}

.dropdown {
    position: absolute;
    background-color: var(--tg-theme-bg-color);    
    z-index: 1000;
    top: 110%;
    left: 16px;
    right: 17px;    
    border-radius: 10px;
    padding: 0;
}

.dropdownItem {
    border-bottom: 1px solid var(--tg-theme-secondary-bg-color);    
    display: flex;
    height: 44px;
    margin-left: 16px;
    align-items: center;
    color: var(--tg-theme-text-color);
}

.noBorderBottom {
    border: none;
}