.container {
    margin-bottom: 24px;
}

.containerWithFootnote {
    margin-bottom: 14px;
}

.inpt {
    background-color: var(--tg-theme-bg-color);
    /* background-color: aqua; */
    height: 44px;
    border: none;
    color: var(--tg-theme-text-color);
    font-size: 17px;
    line-height: 22px;
    letter-spacing: -0.43px;
    font-family: 'SF Pro Regular';
    outline: none;
    border-radius: 10px;
    padding-left: 16px;
    margin-top: 7px;
    width: 100%;
    box-sizing: border-box;
    max-width: 100%;
}

.inpt::placeholder {
    color: var(--tg-theme-hint-color);
    font-size: 17px;
    line-height: 22px;
    letter-spacing: -0.43px;
    font-family: 'SF Pro Regular';
}

.ttl {
    margin-left: 7px;
}

.ftnt {
    padding-left: 16px;
    padding-top: 4px;
    color: #3C3C43;
}
