.container {
    margin-bottom: 24px;
}

.btn {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: var(--tg-theme-bg-color);
    /* background-color: aqua; */
    height: 44px;
    border: none;
    
    color: var(--tg-theme-hint-color);
    font-size: 17px;
    line-height: 22px;
    letter-spacing: -0.43px;
    font-family: 'SF Pro Regular';
    border-radius: 10px;
    margin-top: 7px;

    padding-left: 16px;

    padding-right: 17px;

    /* padding: 0 16px; */

    width: 100%;
    box-sizing: border-box;
    max-width: 100%;
}

.ttl {
    margin-left: 7px;
}