.container {
    margin: 0 -15px;
    overflow: hidden;
}

.btn {
    display: flex;
    justify-content: space-between;
    align-items: center;

    height: 44px;
    background-color: var(--tg-theme-bg-color);
    
    border: none;
    width: 100%;

    color: var(--tg-theme-text-color);

    padding-left: 15px;
    padding-right: 16px;
}

.btnText {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 95%;
}

